.contact {
    padding-top: 3%;
    padding-bottom: 10%;
    background-color: #DCDFE5;
}

.contact-me {
    font-weight: normal;
    font-size: 4rem;
    color: #565D6D;
    margin-left: 10%;
    border: solid #565D6D;
    border-width: 0 0 1px 0;
}

@media screen and (min-width: 451px) and (max-width: 600px) {
    .contact-me {
        font-size: 3em;
    }
}

@media screen and (max-width: 450px) {
    .contact-me {
        font-size: 2em;
    }
}

.contact-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.contact-link {
    color: #565D6D;
    position: relative;
    text-align: center;
    cursor: pointer;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    transition: 0.4s ease-in-out;
    text-align: center;
    z-index: 999;
    margin: 10px 20px;
    overflow: hidden;
}

.contact-link:before, .contact-link:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: -100;
    transition: 0.3s ease-in-out;
}

.contact-link:before {
    background-color: #565D6D;
    width: 98%;
    height: 98%;
}

.contact-link:after {
    background-color: #9199AD;
}

.contact-link:hover:after {
    width: 85%;
    height: 85%;
}

.contact-link-content {
    padding: 40px;
}