.homepage {
    top: 0;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-color: #565D6D;
    background-size: 100%;
    height: 100vh;
}

.name {
    position: absolute;
    color: #DCDFE5;
    font-weight: normal;
    overflow: hidden;
}

@media screen and (min-width: 901px) {
    .homepage {
        background-image: url("../../static/forest.jpg");
    }
    .name {
        font-size: 7em;
        height: 42%;
        width: 80%;
        margin: 17% auto auto 6%;
        animation: reveal1 3s 1;
    }
}

@media screen and (min-width: 751px) and (max-width: 900px) {
    .homepage {
        background-image: url("../../static/sunset.JPG");
    }
    .name {
        font-size: 7em;
        height: 42%;
        width: 80%;
        margin: 45% auto auto 6%;
        animation: reveal2 3s 1;
    }
}

@media screen and (min-width: 601px) and (max-width: 750px) {
    .homepage {
        background-image: url("../../static/sunset.JPG");
    }
    .name {
        font-size: 5em;
        height: 42%;
        width: 80%;
        margin: 60% auto auto 6%;
        animation: reveal3 3s 1;
    }
}

@media screen and (min-width: 451px) and (max-width: 600px) {
    .homepage {
        background-image: url("../../static/sunset2.jpg");
    }
    .name {
        font-size: 4em;
        height: 42%;
        width: 90%;
        margin: 60% auto auto 6%;
        animation: reveal4 3s 1;
    }
}

@media screen and (max-width: 450px) {
    .homepage {
        background-image: url("../../static/sunset2.jpg");
    }
    .name {
        font-size: 4em;
        height: 42%;
        width: 90%;
        margin: 75% auto auto 6%;
        animation: reveal5 3s 1;
    }
}

@keyframes reveal1 {
    from {
        opacity: 0;
        margin-top: 25%;
        height: 0%;
    }
    to {
        opacity: 1;
        margin-top: 17%;
        height: 42%;
    }
}

@keyframes reveal2 {
    from {
        opacity: 0;
        margin-top: 50%;
        height: 0%;
    }
    to {
        opacity: 1;
        margin-top: 45%;
        height: 42%;
    }
}

@keyframes reveal3 {
    from {
        opacity: 0;
        margin-top: 66%;
        height: 0%;
    }
    to {
        opacity: 1;
        margin-top: 60%;
        height: 42%;
    }
}

@keyframes reveal4 {
    from {
        opacity: 0;
        margin-top: 66%;
        height: 0%;
    }
    to {
        opacity: 1;
        margin-top: 60%;
        height: 42%;
    }
}

@keyframes reveal5 {
    from {
        opacity: 0;
        margin-top: 80%;
        height: 0%;
    }
    to {
        opacity: 1;
        margin-top: 75%;
        height: 42%;
    }
}