.projects {
    padding-top: 3%;
    padding-bottom: 8%;
    background-color: #565D6D;
}

.my-projects {
    font-weight: normal;
    font-size: 4rem;
    text-align: right;
    color: #DCDFE5;
    margin-right: 10%;
    border: solid #DCDFE5;
    border-width: 0 0 1px 0;
}

.dots {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.project-dot-active, .project-dot-inactive {
    border: 2px solid;
    text-align: center;
    margin: 5px;
    padding: 5px;
    border-radius: 20px;
    width: 16px;
}

.project-dot-active {
    border-color: #DCDFE5;
    color: #DCDFE5;
}

.project-dot-inactive {
    border-color: #9199AD;
    color: #9199AD;
    transition: 0.2s ease-in-out;
}

.project-dot-inactive:hover {
    border-color: #DCDFE5;
}

.project-content {
    padding-bottom: 3%;
}

.project-title {
    text-align: center;
    font-weight: normal;
    color: #DCDFE5;
}

.project-description {
    color: #9199AD;
    font-size: 1.2em;
}

@media screen and (max-width: 500px) {
    .my-projects {
        font-size: 2em;
    }
}

@media screen and (min-width: 1025px) {
    .mountains {
        margin-top: 175px;
    }
}