.navbar-visible {
    top: 0;
    margin-top: 0;
    background-color: #DCDFE5;
    position: fixed;
    height: 100vh;
    z-index: 999;
    transition: 0.5s ease-in-out;
    opacity: 0.85;
}

.navbar-hidden {
    top: 0;
    margin-top: 0;
    background-color: #DCDFE5;
    position: fixed;
    height: 100vh;
    z-index: 999;
    transition: 0.5s ease-in-out;
}

.navbar-link {
    width: 100%;
    text-align: center;
    color: #565D6D;
}

.navbar-link:hover {
    cursor: pointer;
}

.navbar-close {
    padding: 8%;
    right: 50%;
    text-align: right;
}

.navbar-close-x {
    transition: 0.3s ease-in-out;
}

.navbar-close-x:hover {
    transform: rotate(90deg);
}

.navbar-open {
    position: fixed;
    margin: 2%;
    animation: fade 4s 1;
}

@media screen and (min-width: 701px) {
    .navbar-visible {
        width: 20%;
        right: 80%;
    }
    .navbar-hidden {
        width: 20%;
        right: 100%;
    }
    .navbar-link {
        margin: 20% 0;
        font-size: 1.8em;
    }
}

@media screen and (max-width: 700px) {
    .navbar-visible {
        width: 100%;
        right: 0%;
    }
    .navbar-hidden {
        width: 100%;
        right: 100%;
    }
    .navbar-link {
        margin: 15% 0;
        font-size: 2em;
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    60% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}