.bio {
    padding-top: 3%;
    padding-bottom: 5%;
    background-color: #DCDFE5;
}

.about-me {
    font-weight: normal;
    font-size: 4rem;
    color: #565D6D;
    margin-left: 10%;
    border: solid #565D6D;
    border-width: 0 0 1px 0;
}

.trees {
    opacity: 0.5;
    margin: 0 20px;
}

.bio-content {
    color: #565D6D;
}

@media screen and (max-width: 500px) {
    .about-me {
        font-size: 2em;
    }
}