.resume-link {
  color: #565D6D;
  position: absolute;
  font-size: 2em;
  text-align: center;
  cursor: pointer;
  transition: 0.4s ease-in-out;
  z-index: 999;
  animation: fade 5s 1;
}

.resume-link-text {
  text-align: center;
  font-weight: normal;
  margin: 10px;
}

@media screen and (max-width: 415px) {
  .resume-link-text {
    font-size: 0.8em;
  }
}

a {
  text-decoration: none;
}

.resume-link:hover {
  color: #DCDFE5;
}

.resume-link:before {
  content: '';
  position: absolute;
  right: 0;
  height: 100%;
  width: 100%;
  background: #DCDFE5;
  transition: 0.4s ease-in-out;
  z-index: -1;
}

.resume-link:hover:before {
  width: 0%;
  transition: 0.8s ease all;
}

@media screen and (min-width: 901px) {
  .resume-link {
    margin-left: 6%;
    margin-top: 37%;
  }
}

@media screen and (min-width: 751px) and (max-width: 900px) {
  .resume-link {
    margin-left: 7%;
    margin-top: 78%;
  }
}

@media screen and (min-width: 601px) and (max-width: 750px) {
  .resume-link {
    margin-left: 7%;
    margin-top: 87%;
  }
}

@media screen and (min-width: 451px) and (max-width: 600px) {
  .resume-link {
    margin-left: 6%;
    margin-top: 90%;
  }
}

@media screen and (max-width: 450px) {
  .resume-link {
    margin-left: 6%;
    margin-top: 120%;
  }
}

@keyframes fade {
  0% {
      opacity: 0;
  }
  60% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}