.quick-links {
    right: 0;
    text-align: right;
    width: 40%;
    position: fixed;
    margin-top: 2%;
    margin-right: 2%;
    animation: fade 4s 1;
}

.quick-link {
    margin: 0 2%;
    transition: 0.3s ease-in-out;
}

.quick-link:hover {
    transform: scale(1.1);
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    60% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}